<template>
    <div class="register">
        <div class="register-img">
            <div class="register-content">
                <div class="register-title">
                    注册账号
                </div>
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="80px" label-position="left">
                    <el-form-item label="设置账号" prop="userName">
                        <el-input v-model="ruleForm.userName" placeholder="名称在3到15个字符"></el-input>
                    </el-form-item>
                    <el-form-item label="设置密码" prop="password">
                        <el-input type="password" v-model="ruleForm.password" placeholder="密码在7到16个字符"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button
                                type="primary" @click="onSubmit('ruleForm')"
                                v-loading="loading"
                                element-loading-text="账号注册中"
                                element-loading-spinner="el-icon-loading"
                        >注册</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <confirm-dialog ref="confirmDialog" :dialogOpt="dialogOpt"></confirm-dialog>
    </div>
</template>

<script>
import {register} from '@/api/userinfo'
//import {sleep} from "@/utils/shared";
import ConfirmDialog from "@/components/dialog/ComfirmDialog";

export default {
    name: 'register',
    components: {
        ConfirmDialog,
    },
    data() {
        return {
            loading: false,
            dialogOpt: {
                text: '',
                next: 'login'
            },
            ruleForm: {
                userName: '',
                password: ''
            },
            rules: {
                userName: [
                    {required: true, message: '请输入用户名称', trigger: 'blur'},
                    {min: 3, max: 15, message: '长度在 3 到 15 个字符', trigger: 'blur'}
                ],
                password: [
                    {required: true, message: '请输入您的密码', trigger: 'blur'},
                    {min: 7, max: 16, message: '长度在 7 到 16 个字符', trigger: 'blur'}
                ],

            }
        }
    },
    methods: {
        onSubmit(formName) {
            let _self = this;
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    _self.loading = true;
                    register(this.ruleForm).then(res=> {
                        if(res.data.code === 'REGISTER_USERNAME_DUPLICATE') {
                            _self.$message('您输入的名称已被使用，请重新输入！');
                            _self.loading = false;
                        }else {
                            //sleep(3000);
                            _self.dialogOpt.text = '恭喜您，注册成功，是否跳转登录？';
                            this.$refs['confirmDialog'].open();
                            _self.loading = false;
                            //this.$router.push('layout');
                        }
                    }).catch(err=> {
                        _self.$message('网络错误！');
                        _self.loading = false;
                        console.log(err);
                    });
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });

        }
    }

};
</script>
<style scoped lang="less">
.register {
    width: 100%;
    height: 100%;
    background-color: white;
    .register-img {
        width: 100%;
        height: 100vh;
        background: url('../../assets/register/bg.jpg') no-repeat center;
        //background-color: black;
        display: flex;
        flex-flow: column;
        justify-content: center;
        align-items: center;

        .register-content {
            width: 460px;
            padding: 20px 40px 20px 40px;
            border-radius: 10px;
            background-color: white;
            //background: url('../../assets/register/bg.jpg') no-repeat center;
            position: relative;
            .register-title {
                width: 100%;
                height: 32px;
                line-height: 30px;
                font-size: 20px;
                color: #7171d7;

            }

        }
    }


}

</style>