<template>
    <div class="confirm-dialog">
        <el-dialog
                title="提示"
                :visible.sync="dialogVisible"
                width="30%"
                :before-close="handleClose">
            <span class="textTip">{{dialogOpt.text}}</span>
            <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="confirmDone">确 定</el-button>
  </span>
        </el-dialog>
    </div>
</template>
<script>
export default {
    name: "confirm-dialog",
    props: {
        dialogOpt: Object,
    },
    data() {
        return {
            dialogVisible: false
        };
    },
    methods: {
        handleClose() {
            this.$confirm('确认关闭？')
                    .then(res=> {
                        console.log(res)
                    })
                    .catch(err => {
                        console.log(err)
                    });
        },
        confirmDone() {
            this.$router.push(this.dialogOpt.next);
        },
        open() {
            this.dialogVisible = true;
        },
        close() {
            this.dialogVisible = false;
        }
    }
}
</script>

<style scoped lang="less">
.confirm-dialog {
    border-radius: 5px;
    .textTip {
        color: #409eff;
    }
}

</style>