import https from "@/utils/https";

//注册接口
export function register(data) {
    return https({
        url: '/users/register',
        method: 'post',
        transformRequest: [function(data) {
            let ret = '';
            for(let it in data) {
                ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
            return ret;
        }],
        data: data
    });
}

